import DatabaseManager from '../net/DatabaseManager';
import QuestionManager from './QuestionManager';

const db = new DatabaseManager();
const questionManager = new QuestionManager(db);

class QuestionRoute {
  static allQuestion(req, res) {
    questionManager.getAllQuestion((data, err) => {
      if (err) {
        return res.status(500).json({
          success: false,
          message: 'No question found',
        });
      }
      return res.status(200).json({ questions: data });
    });
  }

  static specificQuestion(req, res) {
    const questionId = req.params.id;
    const questionData = {};
    questionManager.getQuestion(questionId, (result) => {
      questionData.success = true;
      questionData.message = 'Questions retrieved';
      questionData.questionId = result.questionid;
      questionData.userId = result.userid;
      questionData.questionTitle = result.questiontitle;
      questionData.questionContent = result.questioncontent;
      questionData.createdAt = result.createdat;
      questionData.answers = [];
      questionManager.getAnswer(questionId, (answer) => {
        if (answer.rows.length > 1) {
          answer.rows.forEach((item) => {
            questionData.answers.push({ item });
          });
          res.status(200).json(questionData);
        }
        if (answer.rows.length === 1) {
          const singleAnswer = answer.rows;
          questionData.answers.push({ singleAnswer });
          res.status(200).json(questionData);
        }
      });
      res.status(200).json(questionData);
    });
  }

  static postQuestion(req, res) {
    const { questionTitle, questionContent } = req.body;
    const { userId } = req.user;
    questionManager.createQuestion(userId, questionTitle, questionContent, (result) => {
      let error = false;
      if (result === 'error') {
        error = true;
      }

      if (error) {
        return res.status(400).json({
          success: false,
          message: 'unable to create question',
        });
      }
      return res.status(200).json({
        success: true,
        message: 'question successfully created',
        userid: result.userid,
        questionid: result.questionid,
        questiontitle: result.questiontitle,
        questioncontent: result.questioncontent,
      });
    });
  }

  static addAnswer(req, res) {
    const questionId = Number(req.params.id);
    const { answer } = req.body;
    questionManager.getQuestion(questionId, (result) => {
      const questionAndAnswer = {
        success: true,
        message: `Answer added to question ${questionId}`,
        questionId: result.questionid,
        userId: result.userid,
        questionTitle: result.questiontitle,
        questionContent: result.questioncontent,
        createdAt: result.createdat,
        answers: [],
      };

      if (result.questiontitle && result.questioncontent) {
        const { userId } = req.user;
        questionManager.createAnswer(userId, questionId, answer, (results, err) => {
          if (results.rows.length > 1) {
            results.rows.forEach((item) => {
              questionAndAnswer.answers.push(item);
            });
            res.status(200).json(questionAndAnswer);
          }
          if (results.rows.length === 1) {
            const singleAnswer = results.rows;
            questionAndAnswer.answers.push(singleAnswer);
            res.status(200).json(questionAndAnswer);
          }
        });
      } else {
        res.status(404).json({
          success: false,
          message: 'No Answer found',
        });
      }
    });
  }

  static getAllQuestionsByUser(req, res) {
    const { userId } = req.user;
    questionManager.getUserQuestions(userId, (error, result) => {
      const userQuestions = result;
      if (error) {
        res.status(500).json({
          success: false,
          message: 'unable to retrieve questions',
        });
      } else {
        res.status(200).json({
          success: true,
          message: 'All Questions by User',
          userQuestions,
        });
      }
    });
  }

  static deleteQuestion(req, res) {
    const { id } = req.params;
    questionManager.deleteOne(id, (result, err) => {
      let isDeleted = '';
      if (result.rowCount === 1) {
        isDeleted = 'deleted';
      }
      if (isDeleted === 'deleted') {
        return res.status(200).json({
          success: true,
          message: 'Question deleted!',
        });
      }
      if (isDeleted === '') {
        return res.status(401).json({
          success: false,
          message: 'Unable to delete question',
        });
      }
    });
  }


  static updateQuestion(req, res) {
    const { questionId, answerId } = req.params;
    const currentUserId = req.user.userId;
    questionManager.getSpecificAnswer(questionId, answerId, (err, answerResult) => {
      req.userAnswer = answerResult;
    });
    questionManager.getQuestion(questionId, (result) => {
      const questionUserId = result.userid;
      if (currentUserId === questionUserId) {
        questionManager.markAnswer(answerId, (ansResult) => {
          if (ansResult === 'successfully marked') {
            res.status(200).json({
              success: true,
              message: 'Answer marked as approved!',
            });
          } else {
            res.status(500).json({
              success: false,
              message: 'unable to mark answer',
            });
          }
        });
      } else if (currentUserId === req.userAnswer.userid) {
        const { answer } = req.body;
        questionManager.updateAnswer(answerId, answer, (ansResult) => {
          if (ansResult === 'answer updated') {
            res.status(200).json({
              success: true,
              message: 'Your answer has been updated',
            });
          } else {
            res.status(500).json({
              success: false,
              message: 'Not updated, try again',
            });
          }
        });
      } else {
        res.status(401).json({
          success: false,
          message: 'Not authorized to accept answer',
        });
      }
    });
  }

  static notFound(req, res) {
    res.status(404).json({
      status: false,
      message: 'Not Found!',
    });
  }
}

export default QuestionRoute;
